window.ya_init = s => {

  ymaps.ready(init);

function init() {
  //   .
  var DELIVERY_TARIFF = 20,
    //  .
    MINIMUM_COST = 500;
  window.myMap = myMap = new ymaps.Map('map', {
    center: [60.906882, 30.067233],
    zoom: 9,
    controls: []
  });
  //   .
  var routePanelControl = new ymaps.control.RoutePanel({
      options: { //   .
        showHeader: true,
        title: ' '
      }
    }),
    zoomControl = new ymaps.control.ZoomControl({
      options: {
        size: 'small',
        float: 'none',
        position: {
          bottom: 145,
          right: 10
        }
      }
    });
  //      .
  routePanelControl.routePanel.options.set({
    types: {
      auto: true
    }
  });

  //       "",   .
  /*routePanelControl.routePanel.state.set({fromEnabled: false,from: ',   16'});*/

  myMap.controls.add(routePanelControl).add(zoomControl);

  //    .
  routePanelControl.routePanel.getRouteAsync().then(function (route) {

    //     ,  .
    route.model.setParams({ results: 1}, true);
    //      .
    route.model.events.add('requestsuccess', function () {
      var activeRoute = route.getActiveRoute();
      if (activeRoute) {
        //   .
        var rte = route.getActiveRoute();
        console.log('rte->', rte);
        console.log('rte.properties->', rte.properties);
        var rtedata = rte.properties._data;
        v._data.x1 = x1 = rtedata.boundedBy["0"]["0"]
        v._data.y1 = y1 = rtedata.boundedBy["0"][1]
        v._data.x2 = x2 = rtedata.boundedBy[1]["0"]
        v._data.y2 = y2 = rtedata.boundedBy[1]["1"]
        v.distance_text = rtedata.distance.text;
        v.distance_val = rtedata.distance.value;

        v.duration_text = rtedata.duration.text;
        v.duration_val = rtedata.duration.value;

        v.durationInTraffic_text = rtedata.durationInTraffic.text;
        v.durationInTraffic_val = rtedata.durationInTraffic.value;
        var length = route.getActiveRoute().properties.get("distance");
        //   .
        var price = calculate(Math.round(length.value / 1000)); //v._data.price = price;
        //     .
        var balloonContentLayout = ymaps.templateLayoutFactory.createClass(
          '<span>: ' + length.text + '.</span><br/>' +
          '<span style="font-weight: bold; font-style: italic">: ' + price + ' .</span>');
        //      .
        route.options.set('routeBalloonContentLayout', balloonContentLayout);
        //activeRoute.balloon.open();//  .
        v.calculate_cost();
        getAddress([x1, y1]); //getAddress([55.753994, 37.622093])
        getAddress([x2, y2]);
      }
    });

  });
  // ,   .
  function calculate(routeLength) {console.log(routeLength);return Math.max(routeLength * DELIVERY_TARIFF, MINIMUM_COST);  }
}



function getAddress(coords) {
  ymaps.geocode(coords).then(function (res) {
    firstGeoObject = v._data.firstGeoObject = res.geoObjects.get(0).properties._data.text;
    secontGeoObject = v._data.secontGeoObject = res.geoObjects.get(1).properties._data.text;
    v._data.firstGeoObject = document.querySelectorAll('.ymaps-2-1-64-route-panel-input__input')[0].value
    v._data.secontGeoObject = document.querySelectorAll('.ymaps-2-1-64-route-panel-input__input')[1].value
    console.col('firstGeoObject=>', firstGeoObject, 'secontGeoObject=>', secontGeoObject);
  });
}

}



